import request from '@/plugin/axios';
import publicService from "@/api/sys/service.js";

const service = {
    ...publicService,
    /**
     * 分页获取矛盾纠纷排查化解列表
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageContradictionResolve(param, successFunc) {
        request({
            url: "/compre/peace/solve/findPage?name="+ param.queryForm.name +"&districtCode="+ param.queryForm.districtCode +"&page="+ param.page +"&pageSize="+ param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加矛盾纠纷排查化解
     * 
     * @param {any} param 矛盾纠纷排查化解参数
     * @param {any} successFunc 回调函数
     */
    requestSaveContradictionResolve(param, successFunc) {
        request({
            url: "/compre/peace/solve/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改矛盾纠纷排查化解
     * 
     * @param {any} param 矛盾纠纷排查化解参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateContradictionResolve(param, successFunc) {
        request({
            url: "/compre/peace/solve/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 删除矛盾纠纷排查化解
     * 
     * @param {any} peopleId 矛盾纠纷排查化解 ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteContradictionResolve(peopleId, successFunc) {
        request({
            url: "/compre/peace/solve/delete?id="+ peopleId,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    }
};

export default service;