<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" fullscreen @close="resetForm">
        <el-tabs v-model="activeName">
            <el-tab-pane label="事件信息" name="first">
                <el-form ref="caseForm" :model="dialogForm" label-width="150px" :rules="rules">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="事件名称" prop="name">
                                <el-input v-model="dialogForm.name" placeholder="请填写事件名称" maxlength="100"></el-input>
                            </el-form-item>
                            <el-form-item label="事件规模" prop="caseScale">
                                <el-select placeholder="请选择事件规模" v-model="dialogForm.caseScale">
                                    <el-option v-for="(item, index) in dic['事件规模']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="发生日期" prop="happenDate">
                                <el-date-picker v-model="dialogForm.happenDate" type="date" placeholder="请选择发生日期"></el-date-picker>
                            </el-form-item>
                            <el-form-item label="事件类别" prop="caseType">
                                <el-cascader :options="dic['事件类别']" :props="{value: 'code'}" filterable clearable
                                    v-model="dialogForm.caseType"></el-cascader>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="发生地点" prop="happenAddress">
                                <el-input v-model="dialogForm.happenAddress" placeholder="请填写发生地点" maxlength="100"></el-input>
                            </el-form-item>
                            <el-form-item label="涉及人数" prop="involvePeopleCount">
                                <el-input v-model="dialogForm.involvePeopleCount" placeholder="请填写涉及人数" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="16">
                            <el-form-item label="事件简述" prop="eventSketch">
                                <el-input v-model="dialogForm.eventSketch" type="textarea" :rows="3" placeholder="请填写事件简述"
                                    maxlength="4000"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="涉及单位">
                                <el-input v-model="dialogForm.involveDept" placeholder="请填写涉及单位" maxlength="100"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="主要当事人信息" name="second">
                <el-form ref="dialogForm" :model="dialogForm" label-width="150px" :rules="rules">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="主要当事人证件类型" prop="partiesCardCode">
                                <el-select v-model="dialogForm.partiesCardCode" placeholder="请选择主要当事人证件类型">
                                    <el-option v-for="(item, index) in dic['证件代码']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="主要当事人性别" prop="partiesSex">
                                <el-select placeholder="请选择主要当事人性别" v-model="dialogForm.partiesSex">
                                    <el-option v-for="(item, index) in dic['性别']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="主要当事人人员类别" prop="partiesType">
                                <el-select placeholder="请选择主要当事人人员类别" v-model="dialogForm.partiesType">
                                    <el-option v-for="(item, index) in dic['主要当事人人员类别']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="主要当事人证件号码" prop="partiesCardNo">
                                <el-input v-model="dialogForm.partiesCardNo" placeholder="请填写主要当事人证件号码" maxlength="50"></el-input>
                            </el-form-item>
                            <el-form-item label="主要当事人民族" prop="partiesNation">
                                <el-select placeholder="请选择主要当事人民族" v-model="dialogForm.partiesNation">
                                    <el-option v-for="(item, index) in dic['民族']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="主要当事人居住详址" prop="partiesLiving">
                                <el-input v-model="dialogForm.partiesLiving" placeholder="请填写主要当事人居住详址" maxlength="200"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="主要当事人姓名" prop="partiesName">
                                <el-input v-model="dialogForm.partiesName" placeholder="请填写主要当事人姓名" maxlength="80"></el-input>
                            </el-form-item>
                            <el-form-item label="主要当事人学历">
                                <el-select placeholder="请选择主要当事人学历" v-model="dialogForm.partiesEducation">
                                    <el-option v-for="(item, index) in dic['学历']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="化解信息" name="third">
                <el-form ref="dissolveForm" :model="dialogForm" label-width="150px" :rules="rules">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="化解时限" prop="dissolveDate">
                                <el-date-picker v-model="dialogForm.dissolveDate" type="date" placeholder="请选择化解时限"></el-date-picker>
                            </el-form-item>
                            <el-form-item label="化解责任人姓名" prop="dissolveLeaderName">
                                <el-input v-model="dialogForm.dissolveLeaderName" placeholder="请填写化解责任人姓名" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="化解方式" prop="dissolveWay">
                                <el-cascader :options="dic['化解方式']" :props="{value: 'code'}" filterable clearable
                                    v-model="dialogForm.dissolveWay"></el-cascader>
                            </el-form-item>
                            <el-form-item label="化解负责人联系方式" prop="dissolveLeaderContact">
                                <el-input v-model="dialogForm.dissolveLeaderContact" placeholder="请填写化解负责人联系方式"
                                    maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="化解组织" prop="dissolveOrg">
                                <el-input v-model="dialogForm.dissolveOrg" placeholder="请填写化解组织" maxlength="100"></el-input>
                            </el-form-item>
                            <el-form-item label="化解是否成功">
                                <el-radio-group v-model="dialogForm.isSucceDissolve">
                                    <el-radio :label="0">否</el-radio>
                                    <el-radio :label="1">是</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="16">
                            <el-form-item label="化解情况" prop="dissolveDesc">
                                <el-input v-model="dialogForm.dissolveDesc" type="textarea" :rows="3" placeholder="请填写化解情况"
                                    maxlength="4000"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="16">
                            <el-form-item label="考评意见">
                                <el-input v-model="dialogForm.evaluateDesc" type="textarea" :rows="3" placeholder="请填写考评意见"
                                    maxlength="4000"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="考评日期" prop="evaluateDate">
                                <el-date-picker v-model="dialogForm.evaluateDate" type="date" placeholder="请选择发生日期"></el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <techo-position ref="position" :code="dialogForm.districtCode" :placement="'right'" @formLonLat="getLonlat"></techo-position>
                <div class="lon-lat">
                    <span>经度：</span>
                    <span>{{ dialogForm.x }}</span>
                    <span>经度：</span>
                    <span>{{ dialogForm.y }}</span>
                </div>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
            <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'third'">下一步</el-button>
			<el-button v-if="activeName == 'third'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'third'" type="primary" size="small" @click="activeName = 'second'">上一步</el-button>
            <el-button v-if="activeName == 'third'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/contradiction_resolve_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        事件规模: [],
        事件类别: [],
        证件代码: [],
        性别: [],
        民族: [],
        学历: [],
        主要当事人人员类别: [],
        化解方式: []
      },
      dialogForm: {
        isSucceDissolve: 0,
        x: "",
        y: "",
        districtCode: "",
        happenAddress: "",
        partiesNation: "01",
        partiesCardCode: "111"
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入事件名称",
            trigger: "blur"
          }
        ],
        happenDate: [
          {
            required: true,
            message: "请选择发生日期",
            trigger: "change"
          }
        ],
        happenAddress: [
          {
            required: true,
            message: "请输入发生地点",
            trigger: "blur"
          }
        ],
        caseScale: [
          {
            required: true,
            message: "请选择事件规模",
            trigger: "change"
          }
        ],
        caseType: [
          {
            required: true,
            message: "请选择事件类别",
            trigger: "change"
          }
        ],
        involvePeopleCount: [util.validate.valiInteger()],
        eventSketch: [
          {
            required: true,
            message: "请输入事件简述",
            trigger: "blur"
          }
        ],
        // partiesCardCode: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // partiesCardNo: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        partiesName: [
          {
            required: true,
            message: "请输入主要当事人姓名",
            trigger: "blur"
          }
        ],
        // partiesSex: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // partiesNation: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // partiesType: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // partiesLiving: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // dissolveDate: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // dissolveWay: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // dissolveOrg: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        dissolveLeaderName: [
          {
            required: true,
            message: "请输入",
            trigger: "blur"
          }
        ],
        // dissolveLeaderContact: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // isSucceDissolve: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ],
        // dissolveDesc: [
        //   {
        //     required: true,
        //     message: "请输入",
        //     trigger: "blur"
        //   }
        // ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name === "事件类别" || item.name === "化解方式") {
              this.dic[item.name] = this.handleCascader(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
  },
  watch: {
    isAdd(value) {
      if (value == false) {
        this.$nextTick(() => {
          this.$refs["position"].addMarker({
            lng: this.dialogForm.x,
            lat: this.dialogForm.y
          });
        });
      }
    },
    activeName(name) {
      if (name == "second") {
        this.$refs["caseForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
      } else if(name == "third") {
          this.$refs["dialogForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "second";
            });
          }
        });
      }
    },
    "dialogForm.districtCode": {
      handler(code) {
        if (code) {
          service.requestCodeByAddress(code, data => {
            if (data.belongname) {
              this.dialogForm.happenAddress = data.belongname + data.name;
            } else {
              this.dialogForm.happenAddress = "铜川市";
            }
          });
        }
      }
    }
  },
  methods: {
    handleCascader(arr) {
      let option = arr
        .filter(val => val.code.length === 2)
        .map(val => {
          return {
            code: val.code,
            label: val.name,
            children: []
          };
        });
      let subOption = arr
        .filter(val => val.code.length !== 2)
        .map(val => {
          return {
            code: val.code,
            label: val.name
          };
        });
      let hash = {};
      option.forEach(val => {
        hash[val.code] = val;
      });
      subOption.forEach(val => {
        let supCode = val.code.substr(0, 2);
        let hashVP = hash[supCode];
        hashVP.children.push(val);
      });
      return option;
    },
    getLonlat(value) {
      this.dialogForm.x = value.lng;
      this.dialogForm.y = value.lat;
    },
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          param.happenDate = util.time.formatDate(
            param.happenDate,
            "YYYY-MM-DD"
          );
          param.dissolveDate = util.time.formatDate(
            param.dissolveDate,
            "YYYY-MM-DD"
          );
          param.evaluateDate = util.time.formatDate(
            param.evaluateDate,
            "YYYY-MM-DD"
          );
          param.caseType = _.last(param.caseType);
          param.dissolveWay = _.last(param.dissolveWay);
          if (this.isAdd == true) {
            // 添加
            service.requestSaveContradictionResolve(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "矛盾纠纷排查化解添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateContradictionResolve(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "矛盾纠纷排查化解修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.activeName = "first";
      this.$refs["position"].mapApi.clear();
      this.$refs["position"].mapApi.mapOper.setFullExtent();
      this.dialogForm = {
        isSucceDissolve: 0,
        x: "",
        y: "",
        districtCode: "",
        happenAddress: "",
        partiesNation: "01",
        partiesCardCode: "111"
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}

.add-dialog .el-select {
  width: 100%;
}

.add-dialog .el-cascader {
  width: 100%;
}

.add-dialog .lon-lat {
  display: inline-block;
  padding: 0 10px;
  font-size: 16px;
}

.add-dialog .lon-lat span {
  padding-right: 10px;
}
</style>