<template>
  <div class="table-list-page">
    <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
      @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit" @onTreeNode="handleTreeNode">
      <div slot="header">
        <el-row type="flex" justify="space-around" class="techo-list__header-row">
          <el-col :span="10">
            <span class="techo-list__header-col-txt-4">事件名称：</span>
            <div class="techo-list__header-col-left-4">
              <el-input placeholder="请输入事件名称" :maxlength="10" size="small" class="techo-list__header-select-full"
                clearable v-model="queryForm.name"></el-input>
            </div>
          </el-col>
          <el-col :span="9">
          </el-col>
          <el-col :span="5" class="techo-list__header-col-button">
            <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
            <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </techo-list>
    <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
    <vAddDialog ref="vAddDialog"></vAddDialog>
  </div>
</template>

<script>
import service from "./services/contradiction_resolve_service.js";
import vAddDialog from "./contradiction-resolve-add";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "矛盾纠纷排查化解",
      queryForm: {
        districtCode: "",
        name: ""
      },
      header: {
        事件名称: "name",
        发生日期: "happenDate",
        发生地点: "happenAddress",
        事件规模: "dicMap.caseScale",
        事件类别: "dicMap.caseType",
        // 涉及人数: "involvePeopleCount",
        // 事件简述: "eventSketch",
        // 涉及单位: "involveDept",
        // 主要当事人证件类型: "dicMap.partiesCardCode",
        // 主要当事人证件号码: "partiesCardNo",
        主要当事人姓名: "partiesName",
        // 主要当事人性别: "dicMap.partiesSex",
        // 主要当事人民族: "dicMap.partiesNation",
        // 主要当事人学历: "dicMap.partiesEducation",
        // 主要当事人人员类别: "dicMap.partiesType",
        // 主要当事人居住详址: "partiesLiving",
        化解时限: "dissolveDate",
        // 化解方式: "dicMap.dissolveWay",
        // 化解组织: "dissolveOrg",
        化解责任人姓名: "dissolveLeaderName",
        // 化解负责人联系方式: "dissolveLeaderContact",
        化解是否成功: "dicMap.isSucceDissolve"
        // 化解情况: "dissolveDesc",
        // 考评日期: "evaluateDate",
        // 考评意见: "evaluateDesc",
        // 区域代码: "districtCode",
        // 经度: "x",
        // 纬度: "y"
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "resolve"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加矛盾纠纷排查化解 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
    },
    /** 删除矛盾纠纷排查化解 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteContradictionResolve(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "矛盾纠纷排查化解删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "矛盾纠纷排查化解删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      let data = _.cloneDeep(row);
      if (data.caseType) {
        if (data.caseType.length === 4) {
          data.caseType = [data.caseType.substr(0, 2), data.caseType];
        } else {
          data.caseType = [data.caseType];
        }
      } else {
        data.caseType = [];
      }
      if (data.dissolveWay) {
        if (data.dissolveWay.length === 4) {
          data.dissolveWay = [data.dissolveWay.substr(0, 2), data.dissolveWay];
        } else {
          data.dissolveWay = [data.dissolveWay];
        }
      } else {
        data.dissolveWay = [];
      }
      this.$refs["vAddDialog"].dialogForm = data;
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo.queryForm = _.cloneDeep(this.queryForm);
      service.requestPageContradictionResolve(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageContradictionResolve(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              item.dicMap &&
                (item.dicMap.isSucceDissolve =
                  item.isSucceDissolve == 1 ? "是" : "否");
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            item.dicMap &&
              (item.dicMap.isSucceDissolve =
                item.isSucceDissolve == 1 ? "是" : "否");
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>